@font-face {
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 400;
  src: url('../font/pt-sans-v12-latin-regular.eot'); /* IE9 Compat Modes */
  src:
    local(''),
    url('../font/pt-sans-v12-latin-regular.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('../font/pt-sans-v12-latin-regular.woff2') format('woff2'),
    /* Super Modern Browsers */ url('../font/pt-sans-v12-latin-regular.woff')
      format('woff'),
    /* Modern Browsers */ url('../font/pt-sans-v12-latin-regular.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../font/pt-sans-v12-latin-regular.svg#PTSans') format('svg'); /* Legacy iOS */
}

@font-face {
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 700;
  src: url('../font/pt-sans-v12-latin-700.eot'); /* IE9 Compat Modes */
  src:
