    url('../font/pt-sans-v12-latin-700.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */ url('../font/pt-sans-v12-latin-700.woff2') format('woff2'),
    /* Super Modern Browsers */ url('../font/pt-sans-v12-latin-700.woff')
      format('woff'),
    /* Modern Browsers */ url('../font/pt-sans-v12-latin-700.ttf')
      format('truetype'),
    /* Safari, Android, iOS */ url('../font/pt-sans-v12-latin-700.svg#PTSans')
      format('svg'); /* Legacy iOS */
}
